<template>
  <div>
    <m-header title="Toast" :fixed="false"></m-header>
    </br>
    <m-button size="large" @click="toast">默认 Toast</m-button>
    </br>
    <m-button size="large" @click="toast2">自定义 Toast</m-button>
    </br>
    <m-button size="large" @click="toast3">Toast执行后回调</m-button>
  </div>
</template>
<script>
export default {
  name: 'Toast',
  data () {
    return {
    }
  },
  methods: {
    toast () {
      this.$toast('默认 Toast')
    },
    toast2 () {
      this.$toast({text: '自定义 toast', position: 'middle'})
    },
    toast3 () {
      this.$toast({text: '自定义 toast', callback: () => {
        this.$toast({text: 'toast callback !', position: 'middle'})
      }})
    }
  }
}
</script>

